.enter {
	display: flex;
	justify-content: center;
	align-items: center;
	&__form {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		width: 340px;
		height: 450px;
		background: #FFFFFF;
		box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
		border-radius: 12px;
	}
	&__title {
		width: 50px;
		height: 20px;
		font-family: 'Inter';
		font-style: normal;
		font-weight: 500;
		font-size: 20px;
		line-height: 20px;
		text-align: center;
		color: #1E1E1E;
	}
}